import React, { useEffect } from "react";
import {
  addDialog,
  getDialogUsers,
  postMessageToUser,
} from "../../redux/messagesReducer";
import Messages from "./Messages";
import { connect } from "react-redux";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { compose } from "redux";


const MessagesContainer=React.memo((props)=>{
  let curentRef=React.createRef()
  useEffect(()=>{
    props.getDialogUsers()
    let cur=curentRef.current
    cur.scrollTop = cur.scrollHeight;
  },[])
  const onSubmit=(formData)=>{
    // props.addDialog(formData.publishTextarea)
    props.postMessageToUser(formData.body)
  }



  return <Messages curentRef={curentRef} onSubmit={onSubmit} {...props}/>
}
)

let mapStateToProps = (state) => {
  return {
    messagesPage: state.messagesPage,
  };
};
// let mapDispatchToProps = (dispatch) => {
//   return {
//     addDialog: (text) => {
//       dispatch(add_dialog_actionCreator(text));
//     },
//   };
// };

export default compose(
  connect(mapStateToProps, {addDialog,getDialogUsers,postMessageToUser}),
  // withAuthRedirect
)(MessagesContainer);
